<template>
	<div class="emb-register-wrap section-gap">
		<div class="container py-0">
			<v-layout row wrap align-center justify-center>
				<v-flex sm12 md12 lg8 xl7>
					<v-layout row mx-sm-0 mx-3  wrap align-center justify-center>
						
						<v-flex sm10 md5 lg6 xl6>
							<div class="emb-card sign-in-form form-margin d-block white pa-6">
								<h4>Enter Your Category details</h4>
								<v-form ref="form" v-model="valid">
                                    <v-select
									:items="avaliableDepartments"
									label="Departments"
									v-model="assignedDepartments"
									dense
									></v-select>
									<v-text-field
										type="text"
										placeholder="English Name*"
										v-model="category.en"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
									<v-text-field
										type="text"
										v-model="category.kiny"
										placeholder="Kinyarwanda Name*"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
                                    <v-text-field
										type="text"
										v-model="category.fr"
										placeholder="French Name*"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
                                    <v-text-field
										type="text"
										v-model="category.sw"
										placeholder="Swahili Name*"
										:rules="inputRules.basictextRules"
									>
									</v-text-field>
									<v-btn class="accent mx-0 mb-4 mt-4" large  @click.stop.prevent="saveDetails">
										create Category
									</v-btn>
								</v-form>
							</div>
						</v-flex>
					</v-layout>
				</v-flex>
			</v-layout>
		</div>
   </div>
</template>
	
<script>
import department from "Api/department";
import categories from "Api/categories";
	export default{
   	data () {
      	return {
              avaliableDepartments: [],
              assignedDepartments: null,
              selectedDepatmentsId: '',
               category: {
                   en: '',
                   fr: '',
                   kiny: '',
                   sw: '',
                   },
                resDep: null,
         	valid: false,
          	inputRules: {
			   basictextRules: [v => !!v || 'This field should not be empty'],
            }
         }
      },
      methods: {
         saveDetails(){
                this.$refs.form.validate();
                this.createDepartments();
			// 	if(this.valid == true){
			// 		this.$router.push('/');
            // }	
			},
		 async createDepartments () {
           try {
               this.resDep.data.data.forEach(el =>{
                   if (el.name.kiny === this.assignedDepartments) {
                       this.selectedDepatmentsId = el._id
                   }
               })
               const res = await categories
                                .cerateCategories({
                                  department: this.selectedDepatmentsId,
                                  name: this.category
                                })
                console.log(res)
               this.$snotify.success('Add departments process succesfull',{
                    closeOnClick: false,
                    pauseOnHover: false,
                    timeout: 20,
					showProgressBar:false,
					});
               this.$router.push('/admin-panel/categories')
			} catch (err) {
				console.log(err)
                this.$router.push('/')
			}
		}
    },
    async mounted () {
        try {
             this.resDep = await department.getDepartment()
            // console.log(resDep.data.data)
            this.resDep.data.data.forEach(el => {
                this.avaliableDepartments.push(el.name.kiny)
            });
        } catch (err) {
            console.log(err.message)
        }
    }
	}
</script>